@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}
.navbar-main{
    position: fixed;
    top: 0;
    width: 100%;
    padding: 0;
    z-index: 5;
    background-color: white;
}

.navbar-form{
    display: flex;
    flex-direction: row;
}

.navbar-brand{
    font-weight: 700;
    font-size: 1.5rem;
    margin-right: 70px;
}

.search{
    background-color: rgb(238, 238, 238);
    width: 300px;
    font-size: 0.75rem;
}

.nav-link{
    font-size: 14px;
    line-height: 45px;
    padding: 0 11px;
    letter-spacing: 1px;
    cursor: pointer;
    color: rgb(50,50,50);
    font-weight: 500;
}

.navbar{
    padding: 4px 125px 0 125px;
}

a{
    border-bottom: 4px solid transparent;
}

.cat:hover{
    border-bottom: 4px solid gold;
}

.btn-outline-secondary:hover{
    background-color: gold;
    color: white;
    border-color: gold;
}

.dm:hover>.dropdown-menu-m{
    display: block;
}

.dw:hover>.dropdown-menu-w{
    display: block;
}

.mobile-nav{
    display: none !important;
}

.user-section{
    display: flex;
    align-items: center;
}

.bg-dark{
    background-color: black !important;
}

.mobile-signout{
    cursor: pointer;
    margin-bottom: 4px !important;
    padding: 0;
}

.mobile-wishlist{
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 10px !important;
}

.mobile-l-s{
    font-weight: 600;
    border: none;
}

@media screen and (max-width: 992px) {
    .cat{
        border: none;
    }

    .cat:hover{
        border-bottom: none;
    }

    .desk-nav{
        display: none;
    }

    .mobile-nav{
        display: inline-block !important;
        margin-bottom: 5px;
    }
}

@media screen and (max-width: 768px) {
    .navbar{
        padding: 4px 80px 0 80px;
    }
}

@media screen and (max-width: 576px) {
    .navbar{
        padding: 4px 12px 0 12px;
    }
}
.row-dd{
    width: 85vw;
    display: flex;
    justify-content: space-evenly;
    padding-top: 20px;
    padding-bottom: 20px;
}

.dropdown-menu-m{
    left: -205px;
    top: 46px;
}

.dropdown-menu-w{
    left: -258px;
    top: 46px;
}

.dropdown-item{
    font-size: 0.85rem;
    color: rgb(150,150,150);
}

.heading{
    font-weight: 600;
    margin-bottom: 10px;
    color: rgb(50,50,50);
}

sup{
    font-size: 0.5rem;
    color: red;
    font-weight: 600;
}

@media screen and (max-width: 992px) {
    .dropdown-menu-w, .dropdown-menu-m{
        display: none !important;
    }
}
footer{
    background-color: #181818;
    width: 100%;
    bottom: 0;
    color: white;
    padding: 75px 0;
}

.footer-brand{
    font-weight: 700;
    font-size: 1.5rem;
    color: gold;
    cursor: pointer;
}

.footer-heading{
    font-size: 0.75rem;
    color: gold;
    font-weight: 500;
    cursor: pointer;
}

.footer-form{
    font-size: 0.75rem;
}

.footer-form-btn{
    background-color: gold;
    border-radius: 0;
    padding: 5px 25px;
    font-weight: 500;
}

.footer-form-input{
    border-radius: 0;
    background-color: #181818;
    outline: none;
    border: none;
    border-bottom: 1px solid gold;
    font-weight: 500;
}

.footer-form-input:focus{
    background-color: #181818;
}

hr{
    border: 1px solid white;
}

.footer-heading-2{
    font-size: 1.25rem;
    font-weight: 600;
}

.categories, .footer-sec{
    margin-bottom: 10px;
    list-style-type:none;
    font-size: 0.75rem;
    padding: 0;
    margin: 0;
    letter-spacing: 1px;
}

.categories>li, .footer-sec>li, .social{
    cursor: pointer;
}

.footer-img{
    margin-top: 10px;
    width: 100%;
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
    0% {
      transform: translateY(10px);
    }
    100% {
      transform: translateY(-10px);
    }
}
  
@keyframes up-down {
    0% {
      transform: translateY(10px);
    }
    100% {
      transform: translateY(-10px);
    }
}

.footer-form-inline{
    display: flex;
    flex-direction: row;
}

.mobile-categories{
    display: none;
    font-size: 0.75rem;
    letter-spacing: 1px;
}

@media screen and (max-width: 992px) {
    .footer-form-btn{
        padding: 0 2px;
        font-size: 0.55rem;
    }
}

@media screen and (max-width: 768px) {
    footer{
        padding: 15px 0;
    }

    .footer-sec, .footer-email-sec, .footer-hr, .categories{
        display: none;
    }

    .footer-heading {
        margin-bottom: 5px;
    }

    .social-icons{
        margin-right: 10px;
        font-size: 1.15rem !important;
    }

    .footer-heading-2{
        font-size: 0.75rem;
        color: gold;
        margin-bottom: 5px;
        margin-top: 10px;
        font-weight: 700;
    }

    .mobile-categories{
        display: inline-block;
    }
}

@media screen and (max-width: 576px) {
    .footer-container{
        margin: 0;
    }

    footer{
        padding: 10px 0;
    }
}
.carousel{
    margin-top: 50px;
}

.arousel-control-prev-icon, .carousel-control-next-icon{
    cursor: pointer;
}
.home-row{
    text-align: center;
    margin: 65px 0;
}

.view-link{
    text-decoration: none;
    font-weight: 600;
    font-size: 0.75rem;
}

.home-row h5, .home-row h2{
    margin: 0;
}

.home-row > img{
    padding: 15px 0;
}

.options, .trend, .region, .money, .feat, .feat-2{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 10px;
}

.options > img{
    width: 15%;
}

.home-row a{
    cursor: pointer;
}

.trend > img, .region > img, .feat > img{
    width: 30%;
}

.money > img, .feat-2 > img{
    width: 45%;
}

@media screen and (max-width: 576px) {
    .home-container{
        margin: 0;
        padding: 0;
    }

    .home-row h5{
        font-size: 0.85rem;
    }

    .home-row h2{
        font-size: 1.25rem;
    }

    .home-row a{
        font-size: 0.70rem;
    }

    .home-row{
        margin: 30px 0;
    }
}
